.nav {
    /* background: rgba(0, 0, 0, 0.3); */
    background: var(--color__bg_variant);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

.nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.5rem;
    color: var(--color__light);
}

.nav a:hover{
    background-color: rgba(0, 0, 0, 0.2);
}

.nav a.active {
    background: var(--color__primary);
    color: var(--color__bg_variant);
}